import React, { useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import { AlertCircle, ArrowRight, CheckCircle2, FileText, ShieldAlert, Sparkles } from 'lucide-react';

type AnyObj = Record<string, any>;

type Props = {
  caseId: string;
  caseData?: AnyObj | null;
  analysisResult?: AnyObj | null;
  onOpenChat?: () => void;
};

const PLEADING_TYPES = new Set(['claim', 'defense', 'reply', 'response']);
const PLEADING_NAME_HINTS = ['כתב תביעה', 'כתב הגנה', 'כתב תשובה', 'תגובה', 'תשובה'];

function asArray<T = any>(v: any): T[] {
  return Array.isArray(v) ? v : [];
}

function normalizeSide(v: any): 'self' | 'opponent' | 'court' | 'third_party' | 'unknown' {
  const s = String(v || '').toLowerCase();
  if (s === 'self' || s === 'our' || s === 'ours') return 'self';
  if (s === 'opponent' || s === 'other') return 'opponent';
  if (s === 'court') return 'court';
  if (s === 'third_party' || s === 'third-party' || s === 'thirdparty') return 'third_party';
  return 'unknown';
}

function isPleadingFile(f: AnyObj): boolean {
  const dt = String(f?.document_type || f?.doc_type || '').toLowerCase();
  const fn = String(f?.original_filename || f?.filename || f?.name || '').toLowerCase();
  if (PLEADING_TYPES.has(dt)) return true;
  return PLEADING_NAME_HINTS.some((h) => fn.includes(h));
}

function getDisplayName(f: AnyObj): string {
  return String(f?.original_filename || f?.filename || f?.name || 'מסמך');
}

function severityRank(v: any): number {
  const s = String(v || '').toUpperCase();
  if (s === 'CRITICAL') return 4;
  if (s === 'HIGH') return 3;
  if (s === 'MEDIUM') return 2;
  if (s === 'LOW') return 1;
  return 0;
}

function pickText(item: AnyObj): string {
  return (
    item?.title ||
    item?.summary ||
    item?.description ||
    item?.pattern ||
    item?.text ||
    item?.claim ||
    item?.dispute ||
    ''
  );
}

export default function ConclusionsView({ caseId, caseData, analysisResult, onOpenChat }: Props) {
  const navigate = useNavigate();

  const files = useMemo(() => asArray(caseData?.files), [caseData]);

  const contradictions = useMemo(() => asArray(analysisResult?.contradictions), [analysisResult]);
  const disputes = useMemo(() => asArray(analysisResult?.disputes), [analysisResult]);
  const agreedFacts = useMemo(() => asArray(analysisResult?.agreed_facts), [analysisResult]);
  const redFlags = useMemo(() => asArray(analysisResult?.red_flags), [analysisResult]);
  const deception = useMemo(() => asArray(analysisResult?.deception_patterns), [analysisResult]);
  const insights = useMemo(() => asArray(analysisResult?.insights), [analysisResult]);

  const pleadingCoverage = useMemo(() => {
    const out = {
      self: 0,
      opponent: 0,
      court: 0,
      third_party: 0,
      unknown: 0,
      selfAny: 0,
      opponentAny: 0,
      hasAnyPleading: false,
      hasSelfPleading: false,
      hasOpponentPleading: false,
      pleadingFiles: [] as AnyObj[],
    };

    for (const f of files) {
      const side = normalizeSide(f?.source || f?.owner || f?.party || f?.uploaded_by_side);
      if (side === 'self') out.selfAny += 1;
      if (side === 'opponent') out.opponentAny += 1;
      if (!isPleadingFile(f)) continue;
      out.hasAnyPleading = true;
      out.pleadingFiles.push(f);
      if (side === 'self') {
        out.self += 1;
        out.hasSelfPleading = true;
      } else if (side === 'opponent') {
        out.opponent += 1;
        out.hasOpponentPleading = true;
      } else if (side === 'court') {
        out.court += 1;
      } else if (side === 'third_party') {
        out.third_party += 1;
      } else {
        out.unknown += 1;
      }
    }

    return out;
  }, [files]);

  const topCritical = useMemo(() => {
    const scored = contradictions
      .map((c: AnyObj) => {
        const score = typeof c?.score === 'number' ? c.score : undefined;
        const sev = c?.severity ?? c?.importance;
        const rank = severityRank(sev);
        const rankFromScore = typeof score === 'number' ? (score >= 85 ? 4 : score >= 70 ? 3 : score >= 50 ? 2 : 1) : 0;
        return { c, weight: Math.max(rank, rankFromScore) };
      })
      .sort((a, b) => b.weight - a.weight);

    return scored.filter((x) => x.weight >= 3).slice(0, 5).map((x) => x.c);
  }, [contradictions]);

  const topDisputes = useMemo(() => {
    return disputes
      .slice()
      .sort((a: AnyObj, b: AnyObj) => severityRank(b?.importance) - severityRank(a?.importance))
      .slice(0, 6);
  }, [disputes]);

  const topAgreed = useMemo(() => agreedFacts.slice(0, 6), [agreedFacts]);
  const topRedFlags = useMemo(() => redFlags.slice(0, 6), [redFlags]);
  const topDeception = useMemo(() => deception.slice(0, 6), [deception]);

  const missing = useMemo(() => {
    const items: { title: string; detail?: string; cta?: { label: string; onClick: () => void } }[] = [];

    if (!pleadingCoverage.hasSelfPleading) {
      items.push({
        title: 'אין עדיין כתב טענות שלנו (כתב תביעה/תגובה/תשובה)',
        detail: 'כדי שהמערכת תוכל להפיק סתירות/פלוגתאות/אסטרטגיה בצורה אמינה, מומלץ להתחיל מכתבי הטענות.',
        cta: {
          label: 'העלה מסמכים',
          onClick: () => navigate(`/cases/${caseId}/files?upload=true`),
        },
      });
    }

    if (!pleadingCoverage.hasOpponentPleading) {
      items.push({
        title: 'אין עדיין כתב טענות של הצד השני (כתב הגנה/תגובה)',
        detail: 'סיכון ותכנון מהלכים חזקים דורשים מסמכי יריב. אם העלית—ודא שסימנת “שייך ליריב” בהעלאה.',
        cta: {
          label: 'העלה מסמכי יריב',
          onClick: () => navigate(`/cases/${caseId}/files?upload=true`),
        },
      });
    }

    if (files.length === 0) {
      items.unshift({
        title: 'אין מסמכים בתיק עדיין',
        detail: 'העלה את כתבי הטענות והמסמכים המרכזיים (הסכמים/התכתבויות/פרוטוקולים) ואז הרץ ניתוח.',
        cta: {
          label: 'העלה מסמכים',
          onClick: () => navigate(`/cases/${caseId}/files?upload=true`),
        },
      });
    }

    return items;
  }, [pleadingCoverage, files.length, navigate, caseId]);

  const nextActions = useMemo(() => {
    const actions: { title: string; detail?: string; onClick?: () => void }[] = [];

    if (topCritical.length > 0) {
      actions.push({
        title: `לשבת על ${topCritical.length} סתירות משמעותיות ולהחליט קו חקירה/תגובה`,
        detail: 'מומלץ להתחיל בסתירות עם חומרה גבוהה/תמיכה טובה במקור.',
        onClick: () => navigate(`/cases/${caseId}/contradictions`),
      });
    }

    if (topDisputes.length > 0) {
      actions.push({
        title: `לנסח עמדה ל-${topDisputes.length} פלוגתאות מרכזיות`,
        detail: 'כל פלוגתא = מה נדרש להוכיח + איזה מסמך/עד יבסס.',
        onClick: () => navigate(`/cases/${caseId}/disputes`),
      });
    }

    if (pleadingCoverage.hasSelfPleading && (disputes.length > 0 || contradictions.length > 0 || deception.length > 0)) {
      actions.push({
        title: 'להפיק שאלות לחקירה נגדית (רק אם יש טענות + סתירות/פלוגתאות/דפוסי שקר)',
        detail: 'אפשר לבצע מתוך הצ׳אט (כפתור “שאלות”/תפריט פעולות מהירות).',
        onClick: () => onOpenChat?.(),
      });
    }

    if (pleadingCoverage.hasSelfPleading) {
      actions.push({
        title: 'לייצר אסטרטגיה ארוכת טווח (מספר דרכי פעולה + תורת המשחקים)',
        detail: 'מומלץ לאחר שיש לפחות כתב תביעה + שם/פרופיל עו״ד יריב + אותות תקיפה (סתירות/פלוגתאות/שקר).',
        onClick: () => onOpenChat?.(),
      });
    }

    if (pleadingCoverage.hasSelfPleading && pleadingCoverage.hasOpponentPleading) {
      actions.push({
        title: 'בדיקת סיכון (רק אם יש כתבי טענות משני הצדדים)',
        onClick: () => navigate(`/cases/${caseId}/risk`),
      });
    }

    // If nothing obvious, guide to upload + chat
    if (actions.length === 0) {
      actions.push({
        title: 'המשך מומלץ: העלאת כתבי טענות → ניתוח → סתירות/פלוגתאות → שאלות/אסטרטגיה',
        onClick: () => navigate(`/cases/${caseId}/files?upload=true`),
      });
    }

    return actions.slice(0, 6);
  }, [topCritical.length, topDisputes.length, disputes.length, contradictions.length, deception.length, pleadingCoverage, navigate, caseId, onOpenChat]);

  return (
    <div className="space-y-6">
      <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
        <div className="flex items-start justify-between gap-4">
          <div>
            <h2 className="text-2xl font-bold text-white flex items-center gap-2">
              <span className="inline-flex items-center justify-center w-10 h-10 rounded-xl bg-emerald-500/20 border border-emerald-500/30">
                <Sparkles className="text-emerald-300" size={20} />
              </span>
              מסקנות (תכל׳ס)
            </h2>
            <p className="text-white/70 text-sm mt-2">
              תמונת מצב קצרה + מה קריטי עכשיו + מה הצעד הבא, בלי להפוך את זה לשיחה ארוכה.
            </p>
          </div>

          <div className="flex gap-2">
            <button
              onClick={() => navigate(`/cases/${caseId}/chat`)}
              className="px-4 py-2 rounded-lg bg-white/5 hover:bg-white/10 border border-white/10 text-white/90 text-sm"
              title="פתח צ׳אט"
            >
              לצ׳אט
            </button>
            <button
              onClick={() => navigate(`/cases/${caseId}/files?upload=true`)}
              className="px-4 py-2 rounded-lg bg-purple-600/80 hover:bg-purple-600 border border-purple-500/30 text-white text-sm"
              title="העלאת מסמכים"
            >
              העלאת מסמכים
            </button>
          </div>
        </div>

        <div className="grid grid-cols-2 md:grid-cols-6 gap-3 mt-6">
          {[
            { label: 'מסמכים', value: files.length, icon: <FileText size={16} className="text-blue-300" /> },
            { label: 'סתירות', value: contradictions.length, icon: <ShieldAlert size={16} className="text-orange-300" /> },
            { label: 'פלוגתאות', value: disputes.length, icon: <AlertCircle size={16} className="text-amber-300" /> },
            { label: 'מוסכמות', value: agreedFacts.length, icon: <CheckCircle2 size={16} className="text-emerald-300" /> },
            { label: 'דגלים אדומים', value: redFlags.length, icon: <AlertCircle size={16} className="text-red-300" /> },
            { label: 'שקר/הטעיה', value: deception.length, icon: <AlertCircle size={16} className="text-fuchsia-300" /> },
          ].map((c) => (
            <div key={c.label} className="bg-white/5 rounded-xl p-3 border border-white/10">
              <div className="flex items-center justify-between">
                <div className="text-white/70 text-xs">{c.label}</div>
                {c.icon}
              </div>
              <div className="text-white text-xl font-bold mt-1">{c.value}</div>
            </div>
          ))}
        </div>
      </div>

      {missing.length > 0 && (
        <div className="bg-red-500/10 backdrop-blur-xl rounded-2xl p-6 border border-red-500/30">
          <h3 className="text-lg font-bold text-white mb-3">מה חסר כדי להגיע לתוצר חזק</h3>
          <div className="space-y-3">
            {missing.map((m, idx) => (
              <div key={idx} className="bg-white/5 rounded-xl p-4 border border-white/10">
                <div className="flex items-start justify-between gap-3">
                  <div>
                    <div className="text-white font-semibold">{m.title}</div>
                    {m.detail && <div className="text-white/70 text-sm mt-1">{m.detail}</div>}
                  </div>
                  {m.cta && (
                    <button
                      onClick={m.cta.onClick}
                      className="px-3 py-2 rounded-lg bg-white/5 hover:bg-white/10 border border-white/10 text-white/90 text-sm whitespace-nowrap"
                    >
                      {m.cta.label} <ArrowRight size={14} className="inline" />
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
          <h3 className="text-lg font-bold text-white mb-3">מה עושים עכשיו (Top פעולות)</h3>
          <div className="space-y-3">
            {nextActions.map((a, idx) => (
              <button
                key={idx}
                onClick={a.onClick}
                className={`w-full text-right bg-white/5 hover:bg-white/10 rounded-xl p-4 border border-white/10 transition ${
                  a.onClick ? 'cursor-pointer' : 'cursor-default'
                }`}
              >
                <div className="text-white font-semibold">{a.title}</div>
                {a.detail && <div className="text-white/70 text-sm mt-1">{a.detail}</div>}
              </button>
            ))}
          </div>

          {pleadingCoverage.pleadingFiles.length > 0 && (
            <details className="mt-5 bg-white/5 rounded-xl p-4 border border-white/10">
              <summary className="text-white/80 cursor-pointer">כתבי טענות שזוהו בתיק</summary>
              <div className="mt-3 space-y-2">
                {pleadingCoverage.pleadingFiles.slice(0, 8).map((f, idx) => (
                  <div key={idx} className="text-white/70 text-sm flex items-center justify-between gap-3">
                    <span className="truncate">{getDisplayName(f)}</span>
                    <span className="text-white/40 text-xs">{String(f?.document_type || f?.doc_type || '').toLowerCase() || 'pleading'}</span>
                  </div>
                ))}
                {pleadingCoverage.pleadingFiles.length > 8 && (
                  <div className="text-white/50 text-xs">+ עוד {pleadingCoverage.pleadingFiles.length - 8}…</div>
                )}
              </div>
            </details>
          )}
        </div>

        <div className="space-y-6">
          <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
            <h3 className="text-lg font-bold text-white mb-3">מה קריטי (סיכום קצר)</h3>

            {topCritical.length === 0 && topDisputes.length === 0 && topRedFlags.length === 0 ? (
              <div className="text-white/70 text-sm">עדיין אין מספיק איתותים חזקים (או שלא רץ ניתוח). העלאה + ניתוח יסדרו את זה.</div>
            ) : (
              <div className="space-y-4">
                {topCritical.length > 0 && (
                  <div>
                    <div className="text-orange-200 font-semibold mb-2">סתירות משמעותיות</div>
                    <ul className="space-y-2">
                      {topCritical.map((c: AnyObj, idx: number) => (
                        <li key={c?.id || idx} className="bg-white/5 rounded-xl p-3 border border-white/10">
                          <div className="text-white/90 text-sm">{pickText(c) || 'סתירה'}</div>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}

                {topDisputes.length > 0 && (
                  <div>
                    <div className="text-amber-200 font-semibold mb-2">פלוגתאות מרכזיות</div>
                    <ul className="space-y-2">
                      {topDisputes.map((d: AnyObj, idx: number) => (
                        <li key={d?.id || idx} className="bg-white/5 rounded-xl p-3 border border-white/10">
                          <div className="text-white/90 text-sm">{pickText(d) || 'פלוגתא'}</div>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}

                {topRedFlags.length > 0 && (
                  <div>
                    <div className="text-red-200 font-semibold mb-2">דגלים אדומים</div>
                    <ul className="space-y-2">
                      {topRedFlags.map((rf: AnyObj, idx: number) => (
                        <li key={rf?.id || idx} className="bg-white/5 rounded-xl p-3 border border-white/10">
                          <div className="text-white/90 text-sm">{pickText(rf) || 'דגל אדום'}</div>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}

                {topDeception.length > 0 && (
                  <div>
                    <div className="text-fuchsia-200 font-semibold mb-2">דפוסי שקר/הטעיה</div>
                    <ul className="space-y-2">
                      {topDeception.map((p: AnyObj, idx: number) => (
                        <li key={p?.id || idx} className="bg-white/5 rounded-xl p-3 border border-white/10">
                          <div className="text-white/90 text-sm">{pickText(p) || 'דפוס'}</div>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            )}
          </div>

          <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
            <h3 className="text-lg font-bold text-white mb-3">מה כבר יציב (עוגנים)</h3>
            {topAgreed.length === 0 ? (
              <div className="text-white/70 text-sm">אין מוסכמות שזוהו עדיין.</div>
            ) : (
              <ul className="space-y-2">
                {topAgreed.map((a: AnyObj, idx: number) => (
                  <li key={a?.id || idx} className="bg-white/5 rounded-xl p-3 border border-white/10">
                    <div className="text-white/90 text-sm">{pickText(a) || 'מוסכמה'}</div>
                  </li>
                ))}
              </ul>
            )}

            {insights.length > 0 && (
              <details className="mt-4 bg-white/5 rounded-xl p-4 border border-white/10">
                <summary className="text-white/80 cursor-pointer">תובנות נוספות ({insights.length})</summary>
                <div className="mt-3 space-y-2">
                  {insights.slice(0, 8).map((it: AnyObj, idx: number) => (
                    <div key={it?.id || idx} className="text-white/80 text-sm bg-white/5 rounded-lg p-3 border border-white/10">
                      {pickText(it) || 'תובנה'}
                    </div>
                  ))}
                </div>
              </details>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
